const ajax = new XMLHttpRequest(); //Î∏åÎùºÏö∞Ï†ÄÍ∞Ä ÎπÑÎèôÍ∏∞Ï≤òÎ¶¨Î•º ÏúÑÌï¥ Ï†úÍ≥µÌïòÎäî ÎÇ¥Ïû•Í∞ùÏ≤¥ÏûÑ
const TITLE_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json';
const contatiner = document.getElementById('root');
const content = document.createElement('div');

//@param url - title or content URL
//@return    - Array(<-JSONÏùÑ Î≥ÄÌôò)
getData = (url) => {
  ajax.open('GET', url, false);
  ajax.send(); //ÏÑúÎ≤ÑÏ∏°Ïóê ÏöîÏ≤≠ Î≥¥ÎÇ¥Í≥† ÏùëÎãµ Í∏∞Îã§Î¶¨Îäî Ï§ë
  return JSON.parse(ajax.response);
};

const newsList = getData(TITLE_URL); //6Î≤àÏóêÏÑú ÏöîÏ≤≠ÌñàÏùÑ Îïå ÏÑúÎ≤ÑÏ∏°Ïùò ÏùëÎãµÏùÑ Î∞õÏïÑÏò§Îäî ÏÜçÏÑ±Ïù¥ responseÏûÑ
console.log(newsList); //ÏÑúÎ≤ÑÏ∏°ÏóêÏÑú Î≥¥ÎÇ∏ ÏùëÎãµÏùÑ Ï∂úÎ†•Ìï¥ Î≥¥Í∏∞ -ArrayÎ°ú Î≥ÄÌôòÌïòÏó¨ Ï∂úÎ†•Ìï¥Ï§å
const ul = document.createElement('ul'); //DOM  APIÍ∞ÄÏßÄÍ≥† ÌÉúÍ∑∏Î•º ÎßåÎìúÎãàÍπå ÏßÅÍ¥ÄÏ†ÅÏù¥ÏßÄ ÏïäÎã§. DOM TreeÍ∑∏Î†§ÏßÄÏßÄ ÏïäÏïÑÏÑú.
window.addEventListener('hashchange', () => {
  const id = location.hash.substring(1);
  const ncontent = getData(CONTENT_URL.replace('@id', id), false);
  const title = document.createElement('h1');
  title.innerHTML = ncontent.title;
  content.appendChild(title);
});
for (let i = 0; i < 5; i++) {
  const li = document.createElement('li'); //liÏÉùÏÑ±
  const a = document.createElement('a'); //a ÏÉùÏÑ±
  a.href = `#${newsList[i].id}`;
  a.innerHTML = `${newsList[i].title}(üëç${newsList[i].comments_count})`;
  li.appendChild(a); // <li><a></a></li>
  ul.appendChild(li); // <ul><li><a></a></li></ul>
} //end of for
contatiner.appendChild(ul); // <div id = 'root'><ul></ul></div>
contatiner.appendChild(content); // <div id = 'root'><ul><div></div></ul></div>
